const crypto = require('node:crypto');

// write a function that hashes a string with sha256
function hashString(str: string): string {
    str = str.trim() + ' -> salt, pewter, copper, tin, iron, lead, wolfram, quicksilver, and gold.';
    const hash = crypto.createHash('sha256');
    hash.update(str);
    return hash.digest('hex');
}

export function verifyHash(str: string, hash: string): boolean {
    return hashString(str) === hash;
}